<template>
	<div class="your-friends">
		<ul class="friends columns">
			<li class="column is-4" v-for="friend in friends" :key="friend.userId">
				<div class="box friend">
					<BaseAvatar />
					{{ friend.user.username }}
					<p class="subtitle">
						{{ friend.user.profile.displayName }}
					</p>
					<button class="button" @click="message(friend.user.username)">
						Message
					</button>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
import { useStore } from "@/store"
import { useRouter } from "vue-router"
export default {
	setup() {
		const profileStore = useStore("Profile")
		const profile = profileStore.profile

		profileStore.fetchFriends()

		const { friends } = profile

		const chatStore = useStore("Chat")
		const router = useRouter()

		const message = (username) => {
			router.push({ name: "Chat", params: {
				partnerUsername: username
			} })
		}

		return {
			friends,
			message,
		}
	},
}
</script>

<style scoped src="./styles.css">
</style>